import Axios from 'axios';
import React, {useState, useEffect} from 'react';
import { useHistory, useParams } from 'react-router-dom';
import styled from 'styled-components'
import './Detail.scss';


let 박스 = styled.div`
    padding: 20px;
`;

let 제목 = styled.h4`
    font-size: 25px;
    font-weight: bold;
    color: ${ props => props.색상 };
`;

// class Detail2 extends React.Compoenet {
//   compoenetDidMount(){}
//   componentWillUnmount(){}
// }


function Detail(props) {

    let [ alert, setAlert ] = useState(true);
    let [ inputData, setInputData ] = useState('');

    useEffect(() => {


      let Timer = setTimeout(()=>{ setAlert(false) }, 3000);
      console.log('실행된다잉!🍒🍄')
      return () => { clearTimeout(Timer) }
    }, []);

    let { id } = useParams();
    let history = useHistory();
    let 찾은상품 = props.shoes.find(function(상품){
        return 상품.id == id
    });
    

    return (
      <div className="container">
          <박스 >
            <제목 className="red">상세페이지입니다</제목>
          </박스>

          <input onChange={(e) => { setInputData(e.target.value) }} />
          { inputData }
          

        {
          alert === true 
          ? (
            <div className="my-alert">
              <p>재고가 얼마 남지 않았어요! 20% SALE</p>
            </div>
            )
          : null
        }
          

        <div className="row">
          <div className="col-md-6">
            <img src="https://codingapple1.github.io/shop/shoes1.jpg" width="100%" />
          </div>
          <div className="col-md-6 mt-4">
            <h4 className="pt-5">{찾은상품.title}</h4>
            <p>{찾은상품.content}</p>
            <p>{찾은상품.price}원</p>

            {/* 컴포넌트 삽입 */}
            <Info stock={props.stock}></Info>


            <button className="btn btn-danger" onClick={()=>{}} >주문하기</button> &nbsp;
            <button className="btn btn-danger" onClick={()=>{ history.push('/'); }}>뒤로가기</button> 
          </div>
        </div>
      </div> 
    )
}

function Info(props) {
  return(
    <p>재고 : {props.stock[0]}개 </p>
  )
}


export default Detail
